import {mapMutations, mapState} from 'vuex'
export default {
	data() {
		return {
			shopDetail: {} // 商品详情对象
		}
	},
	onLoad(option) {
		// console.log(option)
		this.shopDetail = JSON.parse(option.shopItem)
	},
	methods: {
		...mapMutations({
			changeCartList: 'changeCartList'
		}),
		addShopCart(){
			// 触发mutation
			console.log('添加至购物车')
			this.changeCartList(this.shopDetail)
		}
	}	
}
